@import url(https://fonts.googleapis.com/css?family=Work+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #F3F7F9;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

.card-frame {
    display: block;
    overflow: auto;
    margin-left: auto;
    margin-right: auto;
    padding: 2%;
    flex-direction: column;
    text-align: center;
    width: 60vw;
    height:-webkit-fit-content;
    height:-moz-fit-content;
    height:fit-content;
    background: #FFFFFF;
    /* Card Shadow */
    box-shadow: 0px 4px 8px 2px rgba(57, 53, 52, 0.2);
    border-radius: 12px;
}

.alt-logo {
    width: 50%;
}

.card-title {
    font-family: Work Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 2vw;
    text-align: center;
    color: #000000;
}
.login-credentials {
    display: block;
    float: right;
    margin-left: auto;
    margin-right: 0px;
    width: 40%;
}

.login-icon {
    display: block;
    float: left;
    margin-top: 5vh;
    margin-left: 0px;
    margin-right: auto;
    max-width: 40%;
}

.register-prompt {
    font-family: Work Sans;
    font-style: normal;
    font-weight: normal;
    font-size: 21px;
    line-height: 25px;
    color: black;
}
label {
  font-family: Work Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 16px;
  line-height: 22px;
  /* Gray 3 */
  color: #9B9797;
  text-align: left;
}

input[type=text], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  background: #FFFFFF;
  /* Gray 2 */
  border: 3px solid #DCDCDC;
  box-sizing: border-box;
  border-radius: 8px;
  font-family: Work Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 25px;
  /* Gray 3 */
  color: #9B9797;
  transition-duration: 0.4s;
}

input[type=text]:focus {
  outline: none !important;
  /* Aqua */
  border: 3px solid #58CFEA;
  box-sizing: border-box;
  box-shadow: 0px 5px 5px rgba(51, 154, 240, 0.1);
  border-radius: 8px;
}

input[type=email], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  background: #FFFFFF;
  /* Gray 2 */
  border: 3px solid #DCDCDC;
  box-sizing: border-box;
  border-radius: 8px;
  font-family: Work Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 25px;
  /* Gray 3 */
  color: #9B9797;
  background-size: 15px 15px;
  background-position: right;
  background-repeat: no-repeat;
  transition-duration: 0.4s;
}

input[type=email]:focus {
  outline: none !important;
  /* Aqua */
  border: 3px solid #58CFEA;
  box-sizing: border-box;
  box-shadow: 0px 5px 5px rgba(51, 154, 240, 0.1);
  border-radius: 8px;
  background-size: 15px 15px;
  background-position: right;
  background-repeat: no-repeat;
}

input[type=email]:invalid:not(:-ms-input-placeholder) {
  outline: none !important;
  /* Aqua */
  border: 3px solid #FF9A61;
  box-sizing: border-box;
  box-shadow: 0px 1px 1px rgba(240, 61, 62, 0.1);
  border-radius: 8px;
  background-image: url(data:image/png;base64,);
  background-size: 30px 30px;
  background-position: right;
  background-repeat: no-repeat;
}

input[type=email]:invalid:not(:placeholder-shown) {
  outline: none !important;
  /* Aqua */
  border: 3px solid #FF9A61;
  box-sizing: border-box;
  box-shadow: 0px 1px 1px rgba(240, 61, 62, 0.1);
  border-radius: 8px;
  background-image: url(data:image/png;base64,);
  background-size: 30px 30px;
  background-position: right;
  background-repeat: no-repeat;
}

input[type=password], select {
  width: 100%;
  padding: 12px 20px;
  margin: 8px 0;
  display: inline-block;
  background: #FFFFFF;
  /* Gray 2 */
  border: 3px solid #DCDCDC;
  box-sizing: border-box;
  border-radius: 8px;
  font-family: Work Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 25px;
  /* Gray 3 */
  color: #9B9797;
  transition-duration: 0.4s;
}

input[type=password]:focus {
  outline: none !important;
  /* Aqua */
  border: 3px solid #58CFEA;
  box-sizing: border-box;
  box-shadow: 0px 5px 5px rgba(51, 154, 240, 0.1);
  border-radius: 8px;
}

.button-primary {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #1D4FB4;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-family: Work Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 25px;
  transition-duration: 0.4s;
}

.button-primary:hover {
  background-color: #173D9F;
}

.button-primary:disabled {
  opacity: 0.75;
  background-color: #1D4FB4;
}

.button-secondary {
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  background-color: #58CFEA;
  color: white;
  padding: 14px 20px;
  margin: 8px 0;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-family: Work Sans;
  font-style: normal;
  font-weight: normal;
  font-size: 21px;
  line-height: 25px;
  transition-duration: 0.4s;
}

.button-secondary:hover {
  background-color: #3BBCD9;
}
.registration-credentials {
    display: block;
    float: right;
    margin-left: auto;
    margin-right: 0px;
    width: 40%;
}

.registration-icon {
    display: block;
    float: left;
    margin-top: 10vh;
    margin-left: 0px;
    margin-right: auto;
    max-width: 40%;
}
